<template>
  <div class="travel-details">
    <header class="travel-details__header">
      <h2 class="travel-details__title">{{ id }}</h2>

      <status
        class="travel-details__status"
        :status="status"
      />
    </header>

    <div class="travel-details__item">
      <span class="travel-details__label">
        Name:
      </span>
      <span class="travel-details__value">
        {{ name }}
      </span>
    </div>

    <div class="travel-details__item">
      <span class="travel-details__label">
        Destination:
      </span>
      <span class="travel-details__value">
        {{ country }}
      </span>
    </div>

    <div v-if="comment" class="travel-details__item">
      <span class="travel-details__label">
        Note:
      </span>
      <span
        class="travel-details__value"
        :class="{ 'travel-details__value--shrink': comment.length > 50 }"
      >
        {{ comment }}
      </span>
    </div>

    <div class="travel-details__item">
      <span class="travel-details__label">
        Begin:
      </span>
      <span class="travel-details__value">
        {{ startDate }}
      </span>
    </div>

    <div class="travel-details__item">
      <span class="travel-details__label">
        End:
      </span>
      <span class="travel-details__value">
        {{ endDate }}
      </span>
    </div>

    <div v-if="daysLeft" class="travel-details__item">
      <span class="travel-details__label">
        Days to travel:
      </span>
      <span class="travel-details__value">
        {{ daysLeft }}
      </span>
    </div>
  </div>
</template>

<script>
import MappingCountries from '@/components/mixins/MappingCountries'
import Status from '@/components/Status'

export default {
  name: 'TravelDetails',
  components: {
    Status
  },
  mixins: [MappingCountries],
  props: {
    travel: {
      required: true,
      type: Object
    }
  },
  computed: {
    comment () {
      return this.travel.comment;
    },
    country () {
      return this.countries[this.travel.destination];
    },
    daysLeft () {
      return this.travel.daysLeft;
    },
    endDate () {
      return this.humanizeDate(this.travel.endDate);
    },
    id () {
      return `#${this.travel.id}`;
    },
    name () {
      return this.travel.name;
    },
    startDate () {
      return this.humanizeDate(this.travel.startDate);
    },
    status () {
      return this.travel.status;
    }
  },
  methods: {
    humanizeDate(stringDate) {
      const date = new Date(stringDate);
      return date.toLocaleString('en-US', {
        weekday: 'short',
        month: 'long',
        day: '2-digit',
        year: 'numeric'
      });
    }
  }
}
</script>

