<template>
  <header class="travel-footer">
    <loader v-show="isLoading" />

    <div class="travel-footer__actions">
      <button
        class="travel-footer__button travel-footer__button--edit"
        type="button"
        @click="handleEdit(travel.docId)"
      >
        <img
          alt="Edit"
          class="travel-footer__button-icon"
          src="@/assets/icons/icon-edit.svg"
        />
      </button>

      <button
        class="travel-footer__button travel-footer__button--remove"
        type="button"
        @click="handleRemove(travel.docId)"
      >
        <img
          alt="Remove"
          class="travel-footer__button-icon"
          src="@/assets/icons/icon-remove.svg"
        />
      </button>
    </div>
  </header>
</template>

<script>
import Loader from '@/components/Loader'

export default {
  name: 'TravelFooter',
  components: {
    Loader
  },
  props: {
    destroyTravel: {
      required: true,
      type: Function
    },
    travel: {
      required: true,
      type: Object
    },
    toggleBaseModal: {
      required: true,
      type: Function
    },
    toggleTravelFormModal: {
      required: true,
      type: Function
    }
  },
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    handleEdit () {
      this.toggleTravelFormModal();
    },
    handleRemove () {
      this.toggleBaseModal({
        buttonReject: 'Cancel',
        buttonResolve: 'Delete',
        onResolve: this.removeTravel,
        text: 'Are you sure you want to delete your travel?',
        title: 'Delete your travel'
      });
    },
    removeTravel () {
      const { docId } = this.travel;
      this.isLoading = true;

      this.destroyTravel(docId)
        .then(() => {
          this.isLoading = false
          this.$router.replace('/');
        })
        .catch(err => {
          this.isLoading = false
          this.toggleBaseModal({
            buttonResolve: 'Ok',
            text: err.message,
            title: 'Error'
          });
        });
    }
  }
}
</script>

