<template>
  <div class="travel container">
    <h1 class="travel__title">
      <router-link
        class="travel__back-link"
        to="/"
      >
        <img
          alt="Go"
          class="travel__back-link-icon"
          src="@/assets/icons/icon-arrow-back.svg"
        />
        <span class="travel__back-link-text">Back</span>
      </router-link>

      <span class="travel__title-text">Travel Page</span>
    </h1>

    <div v-if="travelCurrent" class="travel__content">
      <travel-details :travel="travelCurrent" />

      <travel-footer
        :destroyTravel="destroyTravel"
        :travel="travelCurrent"
        :toggleBaseModal="toggleBaseModal"
        :toggleTravelFormModal="toggleTravelFormModal"
      />
    </div>

    <div v-else class="travel-fallback">
      <img
        alt="Empty"
        class="travel-fallback__image"
        src="@/assets/icons/icon-cloud-fail.svg"
      />
      <h3 class="travel-fallback__title">Somethin went wrong</h3>
      <p class="travel-fallback__text">
        <span>Please try again later or go to the </span>
        <router-link class="travel-fallback__link" to="/">home page</router-link>
        <span>â€¦</span>
      </p>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import TravelDetails from '@/components/travel/TravelDetails'
import TravelFooter from '@/components/travel/TravelFooter'

export default {
  name: 'Travel',
  components: {
    TravelDetails,
    TravelFooter
  },
  created () {
    const { travelId } = this.$route.params;
    this.SET_CURRENT_TRAVEL(travelId);
  },
  beforeUnmount () {
    this.SET_CURRENT_TRAVEL(null);

    if (this.travelFormModal) {
      this.toggleTravelFormModal();
    }
  },
  computed: {
    ...mapState(['travelCurrent', 'travelFormModal'])
  },
  methods: {
    ...mapActions(['DESTROY_TRAVEL', 'TOGGLE_TRAVEL_FORM_MODAL']),
    ...mapMutations(['SET_CURRENT_TRAVEL', 'TOGGLE_BASE_MODAL']),
    destroyTravel (docId) {
      return this.DESTROY_TRAVEL(docId);
    },
    toggleBaseModal (context) {
      this.TOGGLE_BASE_MODAL(context);
    },
    toggleTravelFormModal () {
      this.TOGGLE_TRAVEL_FORM_MODAL();
    }
  }
}
</script>

